

const SEND_MESSAGE = "SEND_MESSAGE"


export type DialogsPageType = {
    messages: Array<MessagesDataType>
    dialogs: Array<DialogsDataType>
  }

export type DialogsDataType = {
    id: number
    name: string
    avatar: string
}

export type MessagesDataType = {
    id: number
    message: string
}

export type ActionsType =  ReturnType<typeof sendMessageAC>

let initionState:DialogsPageType = {
    messages: [
        { id: 1, message: 'Hi' },
        { id: 2, message: 'Very well' },
        { id: 3, message: 'How are you?' },
    ] as Array<MessagesDataType>,
    dialogs: [
        { id: 1, name: 'Petya', avatar: 'data:image/jpeg;base64,' },
        { id: 2, name: 'Vera', avatar: 'data:image/jpeg;base64,' },
        { id: 3, name: 'Seva', avatar: 'data:image/jpeg;base64,' },
    ] as Array<DialogsDataType>   
}

export const dialogsReducer = (state:DialogsPageType = initionState, action: ActionsType) => {
    switch (action.type) {
       
        case SEND_MESSAGE:
            let body = action.newMessageBody
            return {
                ...state,
                messages: [...state.messages, { id: 4, message: body }]
            }
        default:
            return state;
    }
}

    export const sendMessageAC = (newMessageBody: string) => {
        return {
            type: SEND_MESSAGE,
            newMessageBody
            } as const
    }
   
